
export  default {
    backgroundColor: '#2a3245',
    title: {
        text: '放款时效',
        x: '10',
        top:10,
        textStyle: {
            color: '#fff',
            fontSize: '18'
        },
    },
    tooltip: {
        trigger: 'axis',
        axisPointer: {
            textStyle: {
                color: '#fff',
                fontSize:'14',
            }
        }
    },
    grid: {
        borderWidth: 0,
        top: 110,
        bottom: 95,
        textStyle: {
            color: '#fff',
            fontSize:'14',
        }
    },
    legend: {
        x: '25%',
        top: '7%',
        axisTick:{
            alignWithLabel:'true',
        },
        textStyle: {
            color: '#fff'
        },
        data:[]
    },
    toolbox: {
        right: 20,
        feature: {
            dataView: {
                show: true,
                readOnly: false,
                optionToContent: function(opt) {
                    var axisData = opt.xAxis[0].data;
                    var series = opt.series;
                    var table = '<table style="width:100%;text-align:center"><tbody><tr>'
                        + '<td>时间</td>'
                        + '<td>' + series[0].name + '</td>'
                        + '<td>' + series[1].name + '</td>'
                        + '<td>' + series[2].name + '</td>'
                        + '</tr>';
                    for (var i = 0, l = axisData.length; i < l; i++) {
                        table += '<tr>'
                            + '<td>' + axisData[i] + '</td>'
                            + '<td>' + series[0].data[i] + '</td>'
                            + '<td>' + series[1].data[i] + '</td>'
                            + '<td>' + series[2].data[i] + '</td>'
                            + '</tr>';
                    }
                    table += '</tbody></table>';
                    return table;
                },
                textareaBorderColor:'#fff',
            },
            magicType: {
                type: ['line', 'bar', 'stack', 'tiled']
            },
            restore: {
                show: true
            },
            saveAsImage: {
                pixelRatio: 2
            },

        },
        iconStyle:{
            normal:{
                borderColor:'#fff',
            }
        },
        top:'10',
        right:'40',
    },
    grid: {
        left: '2%',
        right: '100',
        bottom: '50',
        top: '85',
        containLabel: true
    },

    calculable: true,
    xAxis: [{
        type: 'category',
        name:'进件时间/h',
        boundaryGap: false,
        axisLine: {
            lineStyle: {
                color: '#fff'
            }
        },
        data:[]
    }],
    yAxis: [{
        type: 'value',
        name:'耗时/m',
        splitLine: {
            show: true
        },
        axisLine: {
            lineStyle: {
                color: '#fff'
            }
        }
    }],
    dataZoom: [
        {
            show: true,
            height: 30,
            xAxisIndex: [
                0
            ],
            bottom: 5,
            start: 0,
            end: 100,
            handleSize: '110%',
            textStyle: {
                color: '#fff' },
        },
        {
            type: "inside",
            show: true,
            height: 15,
            start: 1,
            end: 35
        },
        {
            type: 'slider',
            show: true,
            yAxisIndex: 0,
            filterMode: 'empty',
            textStyle:{
                color:'#fff',
            },
            width: 15,
            handleSize: 10,
            showDataShadow: false,
            right: '70',
            bottom:'70',
        }
    ],
    series: [
    ]
}
