exports.push([module.id, "\n.el-select .el-input[data-v-6c04b7de] {\n    width: 100px;\n}\ndate-range[data-v-6c04b7de]:after {\n    display: block;\n    content: '.';\n    line-height: 1;\n    clear: both;\n    visibility: hidden;\n}\n.operChartClass[data-v-6c04b7de]{\n    width:100%;\n    height:100%;\n}\n", "",{"version":3,"sources":["src/views/contents/operation/conversionRateCountByDay.vue"],"names":[],"mappings":";AAoDA;IACA,YAAA;AACA;AAEA;IACA,cAAA;IACA,YAAA;IACA,cAAA;IACA,WAAA;IACA,kBAAA;AACA;AAEA;IACA,UAAA;IACA,WAAA;AACA","file":"conversionRateCountByDay.vue?vue&type=style&index=0&id=6c04b7de&scoped=true&lang=css&","sourcesContent":["<template>\r\n    <el-tabs v-model=\"activeName\" @tab-click=\"handleClick\">\r\n        <el-tab-pane label=\"转化率统计\" name=\"first\">\r\n            <div class=\"p_6\">\r\n                <div class=\"search-wrapper\">\r\n                    <div class=\"search-content\">\r\n                        <el-select v-model=\"searchParam.device\" slot=\"prepend\" placeholder=\"设备\" style=\"width: 120px;\">\r\n                            <el-option\r\n                                v-for=\"item in deviceOptions\"\r\n                                :key=\"item.value\"\r\n                                :label=\"item.name\"\r\n                                :value=\"item.value\">\r\n                            </el-option>\r\n                        </el-select>\r\n                        <el-date-picker\r\n                            v-model=\"searchParam.dateRange\"\r\n                            type=\"daterange\"\r\n                            placeholder=\"选择日期范围\">\r\n                        </el-date-picker>\r\n                        <el-select v-model=\"searchParam.rate\" :multiple-limit=\"5\" multiple collapse-tags placeholder=\"请选择统计选项\">\r\n                            <el-option\r\n                                v-for=\"item in countOptions\"\r\n                                :key=\"item.value\"\r\n                                :label=\"item.name\"\r\n                                :value=\"item.value\">\r\n                            </el-option>\r\n                        </el-select>\r\n                    </div>\r\n                    <div class=\"search-btn\">\r\n                        <el-button slot=\"append\" type=\"primary\" icon=\"search\" @click=\"search\">查询</el-button>\r\n                    </div>\r\n                </div>\r\n                <div class='chart-container'>\r\n                    <chart :options=\"line\" ref=\"operLine\" theme=\"ovilia-green\" auto-resize class=\"operChartClass\"></chart>\r\n                    <el-alert\r\n                        title=\"\"\r\n                        type=\"info\"\r\n                        description=\"提示：图表下方和右侧灰色区域两头可拖动控制相应联动区域的缩小和放大，方便数据查看，光标在图表区域内鼠标滑轮滑动亦可以！\"\r\n                        show-icon :closable=\"closable\">\r\n                    </el-alert>\r\n                    <el-alert\r\n                        title=\"\"\r\n                        description=\"警告：图表右上角工具条数据视图点开情况下，请勿点击查询按钮，如点击查询再点击数据视图内刷新后会与前一查询结果重合且图表类型变化，可点击工具条还原按钮还原！\"\r\n                        show-icon type=\"warning\" :closable=\"closable\">\r\n                    </el-alert>\r\n                </div>\r\n            </div>\r\n        </el-tab-pane>\r\n    </el-tabs>\r\n</template>\r\n\r\n<style scoped>\r\n    .el-select .el-input {\r\n        width: 100px;\r\n    }\r\n\r\n    date-range:after {\r\n        display: block;\r\n        content: '.';\r\n        line-height: 1;\r\n        clear: both;\r\n        visibility: hidden;\r\n    }\r\n\r\n    .operChartClass{\r\n        width:100%;\r\n        height:100%;\r\n    }\r\n</style>\r\n\r\n<script>\r\n    import Common from '../../../utils/common'\r\n    import auth from '../../../utils/authenticate'\r\n    import {Notification} from 'element-ui'\r\n\r\n    import 'echarts/lib/component/grid'\r\n    import 'echarts/lib/component/toolbox'\r\n    import 'echarts/lib/component/tooltip'\r\n    import 'echarts/lib/component/title'\r\n    import 'echarts/lib/component/legend'\r\n    import 'echarts/lib/chart/bar'\r\n\r\n    // built-in theme\r\n    import 'echarts/theme/dark'\r\n\r\n    import $ from 'jquery'\r\n    import deepcopy from \"deepcopy\"\r\n    import formatDate from '../../../utils/formatDate.js'\r\n    import initChartData from '../../../data/initChart'\r\n    export default{\r\n        data(){\r\n            let date = new Date();\r\n            let prevMonth = new Date(date.setDate(date.getDate() - 30));\r\n            //let prevMonth = new Date(date.setDate(date.getMonth() - 1));\r\n            let now = new Date();\r\n\r\n            return {\r\n                closable:true,\r\n                deviceOptions: [\r\n                    {value: 'All', name: 'All',},\r\n                    {value: 'iOS',\r\n                        name: 'iOS',},\r\n                    {value: 'Android', name: 'Android'}\r\n                ],\r\n                countOptions:[\r\n                    {value: 'loan_num', name: '借款人申请'},\r\n                    {value: 'Fpid_info', name: '身份证扫描'},\r\n                    {value: 'Fimg_face', name: '人脸识别'},\r\n                    {value: 'Fbase_info', name: '基本信息'},\r\n                    {value: 'Fjob_info', name: '职业信息'},\r\n                    {value: 'Flink_info', name: '联系人信息'},\r\n                    {value: 'Fphone_info', name: '运营商验证'},\r\n                    {value: 'Fband_bank', name: '银行卡信息'},\r\n                    {value: 'Ftaobao_info', name: '淘宝'},\r\n                    {value: 'Femail_info', name: '邮箱'},\r\n                    {value: 'auditing', name: '审核中'},\r\n                    {value: 'auditSuccess', name: '审核成功'},\r\n                    {value: 'auditFail', name: '审核失败'},\r\n                    {value: 'loaning', name: '放款中'},\r\n                    {value: 'loanSuccess', name: '放款成功'},\r\n                    {value: 'loanFail', name: '放款失败'},\r\n                ],\r\n                searchParam: {\r\n                    device: \"\",\r\n                    dateRange: [prevMonth, now],\r\n                    time: '',\r\n                    unit: '',\r\n                    rate:''\r\n                },\r\n                activeName: 'first',\r\n                select: \"\",\r\n                listLoading: true,\r\n                lineGroup: {},\r\n                line:deepcopy(initChartData),\r\n            }\r\n        },\r\n        methods: {\r\n            clearLine(){\r\n                this.line.legend.data = [];\r\n                this.line.xAxis && (this.line.xAxis[0].data = []);\r\n                this.line.series = [];\r\n                this.line.yAxis[0].axisLabel = {\r\n                    formatter: '{value}'\r\n                };\r\n                //格式化tooltip的坐标轴指示器显示数值\r\n                this.line.tooltip.formatter = null;\r\n                this.line.tooltip.axisPointer.label = {formatter: \"{value}\"};\r\n            },\r\n            setDataView(categorys, xAxisData){\r\n                var thead='',trTd=[],tbodyTr='';\r\n                for(var i=0;i<categorys.length;i++){\r\n                    thead+='<td>' + this.line.series[i].name + '</td>';//头部\r\n                }\r\n\r\n                for (var j = 0, l = xAxisData.length; j < l; j++) {\r\n                    trTd[j]='';\r\n                    for(var i=0;i<categorys.length;i++){\r\n                        trTd[j]+='<td>' + this.line.series[i].data[j] + '</td>'\r\n                    }\r\n\r\n                    tbodyTr += '<tr>'\r\n                        +'<td>' + xAxisData[j] + '</td>'\r\n                        +trTd[j]\r\n                        + '</tr>';\r\n                }\r\n                this.line.toolbox.feature.dataView.optionToContent=function() {\r\n                    var table = '<table style=\"width:100%;text-align:center\"><tbody><tr>'\r\n                        + '<td>时间</td>'\r\n                        + thead+'</tr>'\r\n                        +tbodyTr\r\n                        + '</tbody></table>';\r\n                    return table;\r\n                };\r\n            },\r\n            setOptions(categorys, xAxisData){\r\n                let len = xAxisData.length;\r\n                let colors=['rgba(255,144,128,1)','rgba(0,191,183,1)','rgba(252,230,48,1)','rgba(255,73,73,1)','rgba(29,144,230,1)'];\r\n                this.line.xAxis[0].data = xAxisData;\r\n                this.line.legend.data = categorys.map((item) => {\r\n                    return item.value;\r\n                });\r\n                for(var i = 0; i < categorys.length; i++) {\r\n//                    this.line.legend.data[i] = categorys[i].value;\r\n                    this.lineGroup[categorys[i].value] = i;\r\n                    let seriesOptions = {\r\n                        name: categorys[i].value,\r\n                        type: 'line',\r\n                        barMaxWidth: 30,\r\n                        barGap: '80%',\r\n                        symbolSize: 5,\r\n                        symbol: 'circle',\r\n                        data: Array(len).fill(0),\r\n                        itemStyle: {\r\n                            normal: {\r\n                                color:colors[i],\r\n                                label: {\r\n                                    show: true,\r\n                                    position: 'insideTop',\r\n                                    formatter(p) {\r\n                                        return p.value > 0 ? p.value : ''\r\n                                    }\r\n                                }\r\n                            }\r\n                        },\r\n                    };\r\n                    this.line.series.push(seriesOptions);\r\n                }\r\n            },\r\n            search(){\r\n                let self = this;\r\n\r\n                /*if(!self.searchParam.rate || !self.searchParam.rate.length){\r\n                \treturn false;\r\n                }*/\r\n                let dateRange = self.searchParam.dateRange;\r\n                let line = this.$refs.operLine;\r\n                line.showLoading({\r\n                    text: '正在加载',\r\n                    color: '#4ea397',\r\n                    maskColor: 'rgba(255, 255, 255, 0.4)'\r\n                });\r\n                this.listLoading = true;\r\n                let ajaxUrl = '/web/v1/operation/chart/conversionRateByDay';\r\n                let paramData = {\r\n                    device: self.searchParam.device,\r\n                    beginTime: formatDate(dateRange[0], 'yyyy-MM-dd') +' 00:00:00',\r\n                    endTime: formatDate(dateRange[1], 'yyyy-MM-dd') +' 23:59:59',\r\n                    rate: self.searchParam.rate\r\n                };\r\n                Common.ajax({\r\n                    url: ajaxUrl,\r\n                    type: 'POST',\r\n                    contentType: \"application/json\",\r\n                    data: paramData,\r\n                    success:(response)=>{\r\n                        this.$refs.operLine.hideLoading();\r\n                        self.listLoading = false;\r\n                        if (response.data && response.data.list) {\r\n                            this.clearLine();\r\n                            let resData = response.data;\r\n                            this.setOptions(resData.categories, resData.dates);\r\n                            resData.list.forEach((item, index, arr) => {\r\n                                let dataIdx = this.line.xAxis[0].data.indexOf(item.date);\r\n                                if (dataIdx >= 0) {\r\n                                    resData.categories.forEach((option)=>{\r\n                                        this.line.series[this.lineGroup[option.value]].data[dataIdx] = item[option.key];\r\n\r\n                                    });\r\n                                }\r\n                            });\r\n                            this.setDataView(resData.categories, resData.dates);\r\n                        }\r\n                    },\r\n                    error:(err)=>{\r\n                        this.$refs.operLine.hideLoading();\r\n                        self.listLoading = false;\r\n                        let error = err.message || err;\r\n                        Notification.error({\r\n                            title: '错误',\r\n                            message: error\r\n                        });\r\n                    }\r\n                })\r\n            },\r\n            handleClick(tab, event) {\r\n                let index = tab.index;\r\n                if (index == 0) {\r\n                    this.activeName = 'first';\r\n                }\r\n            }\r\n        },\r\n        mounted(){\r\n            let cvHeight = $('.contents').height() - 110;\r\n            $('.operChartClass').css('height', cvHeight);\r\n\r\n            //调用echarts实例resize函数\r\n            this.$refs.operLine.resize();\r\n\r\n            this.search();\r\n            this.line.title.text='转化率统计(日)';\r\n            this.line.xAxis[0].name=\"时间/日\";\r\n            this.line.yAxis[0].name=\"转化率/%\";\r\n        },\r\n        beforeDestroy(){\r\n            this.clearLine();\r\n        },\r\n    }\r\n</script>\r\n"]}]);
