import jQuery from 'jquery'
import auth from './authenticate'

const Common = (function(api,$){
    api.title="好分期报表系统";
    api.ajax = function(options){
        api.token = auth.getToken();
        //var url = '/api' + options.url;
        var url = options.url;
        var _authToken;
        var _timestamp = +new Date();
        if(url.indexOf("api/")>=0){
            var _authTokenObj = auth.getOldToken();
            _timestamp = _authTokenObj.timestamp;
            _authToken = _authTokenObj.token;
            
        }else{
            _authToken = api.token;
        }
        if(!options.uploadFiles){
          if(options.type){
            if(options.type.toLowerCase() =='get'){
              if( typeof options.data === 'object' ){
                // url += api.formatQuery(options.data);
                // options.data = {};
              }
            }else{
              if( typeof options.data === 'object' ) options.data = JSON.stringify(options.data);
            }
          }else{
            if( typeof options.data === 'object' ) options.data = JSON.stringify(options.data);
          }
        }
        
        // if(options.type && options.type.toUpperCase() =='POST'){
        //   if( typeof options.data === 'object' ) options.data = JSON.stringify(options.data);
        // }else{
        //   //url += api.formatQuery(options.data);
        //   //options.data = {};
        // }
      
        $.ajax({
            headers:{
                'Authorization':_authToken,
                'Timestamp':_timestamp,
            },
            url: url,
            type: options.type || "GET",
            traditional: true,
            data: options.data || {},
            async: options.async || true,
            processData: (options.processData === undefined ? true : options.processData),
            contentType: (options.contentType === undefined ? $.ajaxSettings.contentType : options.contentType),
            dataType: options.dataType || 'json',
            success: function (response) {
                if (response.status && response.status !== '0') {
                    if (response.status == -1) {
                      // 登陆失效
                      alert(`警告: ${response.message}`);
                      auth.setAsLoggedOut();
                      location.href = '/#/login';
                    } else if (options.error) {
                      options.error(response);
                    } else {
                      alert(`警告: ${response.message}\n错误：${response.status}`);
                    }
                    return;
                }
                
                options.success && options.success(response);

            },
            error: function (XMLHttpRequest, textStatus, errorThrown) {
                if (options.error) {
                    if(XMLHttpRequest.response) {
                        options.error(XMLHttpRequest.response);
                    }
                    else{
                        options.error({
                            message: '请求失败'
                        });
                    }
                } else {
                }
            },
            complete: options.complete || undefined,
            timeout:options.timeout||null
        });
    };
    api.hasDuplicate= function(obj,arr){
        if(Object.prototype.toString.call(arr) !== "[object Array]"){
            throw new Error('invalid argument');
        }
        for(let i=0,l=arr.length;i<l;i++){
            if(obj == arr[i]){
                return true;
            }
        }
        return false
    };
    api.formatQuery = function(param){
      if(Object.prototype.toString.call(param) !== "[object Object]"){
        throw new Error('invalid argument');
      }
      let searchParam = param;
      let resultQuery = '?';//session=' + api.session;
      for(var i in searchParam){
        if (searchParam.hasOwnProperty(i)) { //filter,只输出man的私有属性
          resultQuery+= i+'='+searchParam[i]+'&';
        }
      }
      
      return resultQuery;
    };

    return api;
})(Common||{}, jQuery);

export default Common;

