const formatDate = function(_datetime,format){
    /* * 使用例子:format="yyyy-MM-dd hh:mm:ss"; */
    if(Object.prototype.toString.call(_datetime) !=="[object Date]" && typeof _datetime !='string'){
      throw  new Error("参数类型不合法！");
    }
  
    var o = {
        "M+" : new Date(_datetime).getMonth() + 1,// month
        "d+" : new Date(_datetime).getDate(), // day
        "h+" : new Date(_datetime).getHours(), // hour
        "m+" : new Date(_datetime).getMinutes(), // minute
        "s+" : new Date(_datetime).getSeconds(), // second
        "q+" : Math.floor((new Date(_datetime).getMonth() + 3) / 3), // quarter
        "S" : new Date(_datetime).getMilliseconds() // millisecond
    }
    if (/(y+)/.test(format)) {
        format = format.replace(RegExp.$1, (new Date(_datetime).getFullYear() + "").substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
        if (new RegExp("(" + k + ")").test(format)) {
            format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
        }
    }
    return format;
}

export default formatDate;
